* {
  font-family: "Roboto";
  color: #fff;
}

body,
html,
#root,
.App {
  background-color: #1e1c24;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.navbar {
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.navbar-brand svg {
  width: 70px;
  display: inline-block;
  cursor: pointer;
}

.navbar-item {
  cursor: pointer;
  margin-right: 1rem;
  font-family: "Segoe UI";
  font-size: 1.1rem;
  transition-duration: 0.5s;
  text-transform: capitalize;
}

.navbar-item:hover {
  color: #5cd89f;
}

.navbar-item:last-child {
  margin-right: 0;
}

.h-100 {
  height: 100vh;
}

.jumbotron {
  padding-top: 100px;
}

.jumbo-left h1,
.jumbo-left h4 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
}

.jumbo-left h1 {
  font-size: 3.3rem;
  margin-bottom: 0;
  margin-top: 50px;
}
.jumbo-left h4 {
  font-size: 1.1rem;
  letter-spacing: 0.5px;
  margin-bottom: 10px;
  margin-top: 0;
}

.jumbo-left button {
  text-transform: uppercase;
  text-decoration: none;
  outline: none;
  border: none;
  font-size: 20px;
  width: 12.5rem;
  height: 3.125rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
  margin-top: 1rem;
  background-color: #5cd89f;
  cursor: pointer;
  box-shadow: 5px 5px 0px 0px rgba(42, 219, 126, 1);
  transition-duration: 0.5s;
}

.jumbo-left button:hover {
  box-shadow: 10px 10px 0px 0px rgba(42, 219, 126, 1);
}

.jumbo-right {
  flex-direction: row;
  justify-content: center;
}

.person {
  width: 10rem;
}

.desk {
  width: 29rem;
}

.front-page-socials {
  position: fixed;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-right: 1.25rem;
  margin-bottom: 1.25rem;
}

.social-item {
  padding: 2px;
}

.social-item svg {
  width: 1.8rem;
  height: 1.8rem;
  cursor: pointer;
}

.social-item svg path {
  fill: #fff;
  transition-duration: 0.4s;
}

.social-item:hover svg path {
  fill: rgba(42, 219, 126, 1);
}

#tsparticles {
  position: absolute;
  width: 100%;
  height: 100%;
}

.page-front {
  z-index: 1020;
}

.preloader {
  background-color: #fff;
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  transition-duration: 0.5s;
}

.preloader.hide {
  opacity: 0;
  visibility: hidden;
}

.preloader img {
  width: 140px;
}

@media (max-width: 768px){.person,.desk{display:none}.navbar{padding:10px 20px}.jumbotron{padding-top:50px}.jumbo-left{display:flex;justify-content:center;align-items:center;flex-direction:column;text-align:center}}

